@use '../utils' as *;

/*******************************************************************************
 HERO SECTION
 ******************************************************************************/

.hero {
    background-color: var(--clr-quaternary);

    .container  {
        display: grid;

        * {
            grid-area: 1 / 1;
        }

        .image {
            width: 100%;
            object-fit: contain;
            display: grid;
            place-items: center;
            padding-block: var(--space-xl);

            & > svg {
                opacity: 0.5;
                padding-block-start: calc(2 * var(--space-xl));

                @include mq('md') {
                    opacity: 1;
                    padding-block-start: 0;
                    transform: translateX(-7vw);
                }
            }
        }

        .content {
            place-self: start center;
            padding-block-start: var(--space);

            //width: 50%;
            position: relative;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            @include mq('md') {
                padding-block-start: var(--space-xl);
                place-self: start end;
            }

            h2, h3 {
                text-align: center;
                line-height: 1em;
            }

            h2 {
                font-size: clamp(1.5rem, 3vw + .1rem, 3rem);
                margin-block-end: var(--space);
                color: var(--clr-primary);
                text-shadow: .05em .05em .05em var(--clr-whitish-a50);
            }

            h3 {
                font-size: clamp(1.25rem, 2vw + .1rem, 2rem);
                margin-block-end: 0;
                color: var(--clr-quinary);
                font-weight: 400;
                font-style: italic;
                margin-block-end: var(--space-l);
                text-shadow: .05em .05em .05em var(--clr-whitish-a25);
            }

            h3 > span {
                color: var(--clr-tertiary);
            }

            .cta, .cta:visited {
                padding-inline: 2em;
                padding-block: 1em;
                background-color: var(--clr-quinary-a25);
                color: var(--clr-tertiary);
                border: var(--border-height) solid var(--clr-tertiary);
                border-radius: var(--border-radius);
                text-transform: uppercase;
                font-weight: 700;
                font-size: clamp(.75rem, 1vw + .1rem, 1.5rem);
            }

            .cta:hover, .cta:active, .cta:focus {
                background-color: var(--clr-tertiary-a50);
                color: var(--clr-quinary);
                outline: none;
            }
        }
    }
}


