body {
    line-height: 1em;
}

h1,
h2,
h3 {
    margin-block: 0 .25em;
    line-height: 1.5em;
    text-wrap: balance; // Recommended UX guideline
}

h1 {
    font-weight: 700;
    font-size: clamp(2.25rem, 2.25rem + .825vw, 3.1875rem);
}

h2 {
    font-weight: 700;
    font-size: clamp(1.6875rem, 1.6875rem + .9375vw, 2.8125rem);
}

h3 {
    font-weight: 700;
    font-size: clamp(1.6875rem, 1.6875rem + .57vw, 2.25rem);
}

p {
    margin-top: 0;
    line-height: 1.5em;
    max-width: clamp(35ch, 100%, 65ch); // Recommended UX guideline (35ch to 75ch, 5ch preferred)
}

a,
a:visited,
a:active {
    text-decoration: none;
    line-height: inherit;
}