@use '../utils' as *;

/*******************************************************************************
 RESET FORM
 ******************************************************************************/

button,
[type="button"],
[type="reset"],
[type="submit"],
[type="file"],
::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit;
    cursor: pointer;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}

[type="checkbox"],
[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

[type="search"] {
    box-sizing: border-box;
    -webkit-appearance: textfield;
    outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

::-webkit-input-placeholder {
    color: inherit;
    opacity: 0.54;
}

/*******************************************************************************
 FORM
 ******************************************************************************/

form {
    margin-block: var(--space-l);
    padding: var(--space);

    background-color: var(--clr-tertiary-a75);
    border: var(--border-height) solid var(--clr-tertiary);
    border-radius: var(--border-radius);
    overflow: hidden;
    font-size: clamp(1.2rem, 2vw + .1rem, 1.5rem);
    font-weight: 700;
    text-shadow: 0 0 .1em var(--clr-blackish-a25);
    
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: var(--space);
    align-items: center;

    @include mq('md') {
        padding: var(--space-l);
    }

    @include mq('lg') {
        padding: var(--space-xl);
        grid-template-columns: auto 1fr;
        grid-template-rows: auto;
    }

    label {
        width: 100%;
        text-align: center;

        @include mq('lg') {
            text-align: right;
        }
    }
    
    input, select, textarea {
        width: 100%;
        box-sizing: border-box;
        padding-inline: 1em;
        padding-block: .5em;
    
        color: var(--clr-quinary);
        border: var(--border-height) solid var(--clr-quinary);
        border-radius: var(--border-radius);
        background-color: var(--clr-quaternary-a50);
        font-size: inherit;
        font-weight: inherit;
        text-shadow: inherit;
    
        &:focus {
            color: var(--clr-quinary);
            border-color: var(--clr-quinary);
            background-color: var(--clr-tertiary);
            outline: 0;
        }
    
        &::placeholder {
            opacity: 1;
        }
    }
    
    textarea {
        resize: vertical;
        font-family: inherit;
    }
    
    input[type=submit] {
        width: fit-content;
        grid-column: 1 / -1;
        justify-self: center;
    
        padding-inline: 2em;
        padding-block: 1em;
        color: var(--clr-quinary);
        border: var(--border-height) solid var(--clr-quinary);
        border-radius: var(--border-radius);
        background-color: var(--clr-quaternary-a50);
        text-transform: uppercase;
        font-weight: 700;
        font-size: clamp(1rem, 1.5vw + .1rem, 1.3rem);
    
        &:hover, &:focus, &:active {
            background-color: var(--clr-tertiary);
        }
    }
}
