@use '../utils' as *;

/*******************************************************************************
 HEADER
 ******************************************************************************/

 :root {
    --header-height: 4rem;

    @include mq('md') {
        --header-height: 5rem;
    }
    @include mq('lg') {
        --header-height: 6rem;
    }
    @include mq('xl') {
        --header-height: 7rem;
    }

    &.mini-header {
        --header-height: 4rem;

        @include mq('xl') {
            --header-height: 5rem;
        }
    }
}

header {
    width: 100%;
    height: var(--header-height);
    background-color: var(--clr-quaternary);
    color: var(--clr-whitish);
    position: sticky;
    top: 0;
    box-shadow: var(--box-shadow);
    z-index: 9999;
    
    .container {
        position: relative;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;

        .logo {
            font-size: 2.2rem;
            width: 10ch;
            height: 2.2rem;
            background-image: url('../assets/images/logo-transparent.png') /*url('../assets/images/logo.svg')*/;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            object-fit: contain;
        }

        .menu {
            display: none;
            @include mq('lg') {
                display: block;
            }

            ul {
                list-style-type: none;
                margin: 0;
                padding: 0;
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                gap: var(--space-xl);

                li a {
                    outline: 0;
                    color: var(--clr-primary);
                    font-size: 1.25rem;
                    line-height: 1em;

                    &:hover, &:active, &:focus {
                        text-decoration: underline;
                    }
                }
            }
        }

        #menu-toggle {
            display: none;
        }

        @include mq-until('lg') {
            #menu-toggle:checked ~ .menu {
                display: block;
                position: fixed;
                inset: var(--header-height) 0 0 0;

                ul {
                    width: 100%;
                    background-color: var(--clr-quaternary);
                    display: flex;
                    flex-direction: column;
                    gap: 0;
                    box-shadow: var(--box-shadow);

                    li {
                        width: 100%;
                        text-align: center;
                        font-size: 1.5rem;
                        padding: 1em;

                        a {
                            font-size: 1.5rem;
                        }
                    }
                }
            }
        }

        .hamburger{
            display: grid;
            align-items: center;

            @include mq('lg') {
                display: none;
            }

            svg {
                stroke: var(--clr-blackish);
            }
        }
    }
}

// Fix scroll points when nav links are clicked
section {
    scroll-margin-top: var(--header-height);
}
