@use '../utils/variables.scss' as *;

:root {
    --clr-primary: #{primary-color()};
    --clr-primary-a25: #{primary-color(.25)};
    --clr-primary-a50: #{primary-color(.50)};
    --clr-primary-a75: #{primary-color(.75)};

    --clr-secondary: #{secondary-color()};
    --clr-secondary-a25: #{secondary-color(.25)};
    --clr-secondary-a50: #{secondary-color(.50)};
    --clr-secondary-a75: #{secondary-color(.75)};

    --clr-tertiary: #{tertiary-color()};
    --clr-tertiary-a25: #{tertiary-color(.25)};
    --clr-tertiary-a50: #{tertiary-color(.50)};
    --clr-tertiary-a75: #{tertiary-color(.75)};

    --clr-quaternary: #{quaternary-color()};
    --clr-quaternary-a25: #{quaternary-color(.25)};
    --clr-quaternary-a50: #{quaternary-color(.50)};
    --clr-quaternary-a75: #{quaternary-color(.75)};

    --clr-quinary: #{quinary-color()};
    --clr-quinary-a25: #{quinary-color(.25)};
    --clr-quinary-a50: #{quinary-color(.50)};
    --clr-quinary-a75: #{quinary-color(.75)};

    --clr-blackish: #{blackish-color()};
    --clr-blackish-a25: #{blackish-color(.25)};
    --clr-blackish-a50: #{blackish-color(.50)};
    --clr-blackish-a75: #{blackish-color(.75)};

    --clr-whitish: #{whitish-color()};
    --clr-whitish-a25: #{whitish-color(.25)};
    --clr-whitish-a50: #{whitish-color(.50)};
    --clr-whitish-a75: #{whitish-color(.75)};
