@use '../utils' as *;

html {
    font-size: 100%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*,
*::before,
*::after {
    box-sizing: inherit;
    transition: var(--transition);
}

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    overflow-y: auto;
}

.wrapper {
    width: 100%;
    min-height: 100dvh;
    margin: 0;
    padding: 0;
    font-family: var(--font-poppins);
    color: var(--clr-blackish);
    background-color: var(--clr-whitish);
}

.container {
    width: 80%;
    height: 100%;
    margin-inline: auto;
    padding-block: var(--space);

    @include mq('md') {
        width: 100%;
        max-width: 600px;
    }

    @include mq('lg') {
        max-width: 960px;
    }

    @include mq('xl') {
        max-width: 1200px;
    }
}

.two-columns {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-auto-rows: dense;
    gap: var(--space-l);
    align-items: start;

    @include mq('md') {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        gap: var(--space-xl);
        align-items: stretch;
    }
}
