:root {
  --font-poppins: "Poppins", sans-serif;
}

:root {
  --clr-primary: hsl(147, 100%, 14%);
  --clr-primary-a25: hsla(147, 100%, 14%, 0.25);
  --clr-primary-a50: hsla(147, 100%, 14%, 0.5);
  --clr-primary-a75: hsla(147, 100%, 14%, 0.75);
  --clr-secondary: hsl(20, 100%, 50%);
  --clr-secondary-a25: hsla(20, 100%, 50%, 0.25);
  --clr-secondary-a50: hsla(20, 100%, 50%, 0.5);
  --clr-secondary-a75: hsla(20, 100%, 50%, 0.75);
  --clr-tertiary: hsl(30, 96%, 56%);
  --clr-tertiary-a25: hsla(30, 96%, 56%, 0.25);
  --clr-tertiary-a50: hsla(30, 96%, 56%, 0.5);
  --clr-tertiary-a75: hsla(30, 96%, 56%, 0.75);
  --clr-quaternary: hsl(14, 100%, 83%);
  --clr-quaternary-a25: hsla(14, 100%, 83%, 0.25);
  --clr-quaternary-a50: hsla(14, 100%, 83%, 0.5);
  --clr-quaternary-a75: hsla(14, 100%, 83%, 0.75);
  --clr-quinary: hsl(31, 100%, 94%);
  --clr-quinary-a25: hsla(31, 100%, 94%, 0.25);
  --clr-quinary-a50: hsla(31, 100%, 94%, 0.5);
  --clr-quinary-a75: hsla(31, 100%, 94%, 0.75);
  --clr-blackish: hsl(0, 0%, 12%);
  --clr-blackish-a25: hsla(0, 0%, 12%, 0.25);
  --clr-blackish-a50: hsla(0, 0%, 12%, 0.5);
  --clr-blackish-a75: hsla(0, 0%, 12%, 0.75);
  --clr-whitish: hsl(0, 0%, 96%);
  --clr-whitish-a25: hsla(0, 0%, 96%, 0.25);
  --clr-whitish-a50: hsla(0, 0%, 96%, 0.5);
  --clr-whitish-a75: hsla(0, 0%, 96%, 0.75);
}

:root {
  --box-shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --box-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
  --box-shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --box-shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --box-shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
  --box-shadow-2xl: 0 25px 50px -12px rgb(0 0 0 / 0.25);
  --box-shadow-inner: inset 0 2px 4px 0 rgb(0 0 0 / 0.05);
  --box-shadow-none: 0 0 #0000;
}

:root {
  --space: 1rem;
  --space-xs: calc(var(--space) / 4);
  --space-s: calc(var(--space) / 2);
  --space-m: var(--space);
  --space-l: calc(var(--space) * 2);
  --space-xl: calc(var(--space) * 4);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  transition: var(--transition);
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.wrapper {
  width: 100%;
  min-height: 100dvh;
  margin: 0;
  padding: 0;
  font-family: var(--font-poppins);
  color: var(--clr-blackish);
  background-color: var(--clr-whitish);
}

.container {
  width: 80%;
  height: 100%;
  margin-inline: auto;
  padding-block: var(--space);
}
@media (width >= 40em) {
  .container {
    width: 100%;
    max-width: 600px;
  }
}
@media (width >= 64em) {
  .container {
    max-width: 960px;
  }
}
@media (width >= 87.5em) {
  .container {
    max-width: 1200px;
  }
}

.two-columns {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-auto-rows: dense;
  gap: var(--space-l);
  align-items: start;
}
@media (width >= 40em) {
  .two-columns {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    gap: var(--space-xl);
    align-items: stretch;
  }
}

body {
  line-height: 1em;
}

h1,
h2,
h3 {
  margin-block: 0 0.25em;
  line-height: 1.5em;
  text-wrap: balance;
}

h1 {
  font-weight: 700;
  font-size: clamp(2.25rem, 2.25rem + 0.825vw, 3.1875rem);
}

h2 {
  font-weight: 700;
  font-size: clamp(1.6875rem, 1.6875rem + 0.9375vw, 2.8125rem);
}

h3 {
  font-weight: 700;
  font-size: clamp(1.6875rem, 1.6875rem + 0.57vw, 2.25rem);
}

p {
  margin-top: 0;
  line-height: 1.5em;
  max-width: clamp(35ch, 100%, 65ch);
}

a,
a:visited,
a:active {
  text-decoration: none;
  line-height: inherit;
}

:root {
  /* BORDERS */
  --border-height: 4px;
  --border-radius: 1rem;
  /* TRANSITIONS */
  --transition: all .2s ease-in-out;
}

/*******************************************************************************
 HEADER
 ******************************************************************************/
:root {
  --header-height: 4rem;
}
@media (width >= 40em) {
  :root {
    --header-height: 5rem;
  }
}
@media (width >= 64em) {
  :root {
    --header-height: 6rem;
  }
}
@media (width >= 87.5em) {
  :root {
    --header-height: 7rem;
  }
}
:root.mini-header {
  --header-height: 4rem;
}
@media (width >= 87.5em) {
  :root.mini-header {
    --header-height: 5rem;
  }
}

header {
  width: 100%;
  height: var(--header-height);
  background-color: var(--clr-quaternary);
  color: var(--clr-whitish);
  position: sticky;
  top: 0;
  box-shadow: var(--box-shadow);
  z-index: 9999;
}
header .container {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
header .container .logo {
  font-size: 2.2rem;
  width: 10ch;
  height: 2.2rem;
  background-image: url("../assets/images/logo-transparent.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  object-fit: contain;
}
header .container .menu {
  display: none;
}
@media (width >= 64em) {
  header .container .menu {
    display: block;
  }
}
header .container .menu ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: var(--space-xl);
}
header .container .menu ul li a {
  outline: 0;
  color: var(--clr-primary);
  font-size: 1.25rem;
  line-height: 1em;
}
header .container .menu ul li a:hover, header .container .menu ul li a:active, header .container .menu ul li a:focus {
  text-decoration: underline;
}
header .container #menu-toggle {
  display: none;
}
@media (width < 64em) {
  header .container #menu-toggle:checked ~ .menu {
    display: block;
    position: fixed;
    inset: var(--header-height) 0 0 0;
  }
  header .container #menu-toggle:checked ~ .menu ul {
    width: 100%;
    background-color: var(--clr-quaternary);
    display: flex;
    flex-direction: column;
    gap: 0;
    box-shadow: var(--box-shadow);
  }
  header .container #menu-toggle:checked ~ .menu ul li {
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    padding: 1em;
  }
  header .container #menu-toggle:checked ~ .menu ul li a {
    font-size: 1.5rem;
  }
}
header .container .hamburger {
  display: grid;
  align-items: center;
}
@media (width >= 64em) {
  header .container .hamburger {
    display: none;
  }
}
header .container .hamburger svg {
  stroke: var(--clr-blackish);
}

section {
  scroll-margin-top: var(--header-height);
}

/*******************************************************************************
 HERO SECTION
 ******************************************************************************/
.hero {
  background-color: var(--clr-quaternary);
}
.hero .container {
  display: grid;
}
.hero .container * {
  grid-area: 1/1;
}
.hero .container .image {
  width: 100%;
  object-fit: contain;
  display: grid;
  place-items: center;
  padding-block: var(--space-xl);
}
.hero .container .image > svg {
  opacity: 0.5;
  padding-block-start: calc(2 * var(--space-xl));
}
@media (width >= 40em) {
  .hero .container .image > svg {
    opacity: 1;
    padding-block-start: 0;
    transform: translateX(-7vw);
  }
}
.hero .container .content {
  place-self: start center;
  padding-block-start: var(--space);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (width >= 40em) {
  .hero .container .content {
    padding-block-start: var(--space-xl);
    place-self: start end;
  }
}
.hero .container .content h2, .hero .container .content h3 {
  text-align: center;
  line-height: 1em;
}
.hero .container .content h2 {
  font-size: clamp(1.5rem, 3vw + 0.1rem, 3rem);
  margin-block-end: var(--space);
  color: var(--clr-primary);
  text-shadow: 0.05em 0.05em 0.05em var(--clr-whitish-a50);
}
.hero .container .content h3 {
  font-size: clamp(1.25rem, 2vw + 0.1rem, 2rem);
  margin-block-end: 0;
  color: var(--clr-quinary);
  font-weight: 400;
  font-style: italic;
  margin-block-end: var(--space-l);
  text-shadow: 0.05em 0.05em 0.05em var(--clr-whitish-a25);
}
.hero .container .content h3 > span {
  color: var(--clr-tertiary);
}
.hero .container .content .cta, .hero .container .content .cta:visited {
  padding-inline: 2em;
  padding-block: 1em;
  background-color: var(--clr-quinary-a25);
  color: var(--clr-tertiary);
  border: var(--border-height) solid var(--clr-tertiary);
  border-radius: var(--border-radius);
  text-transform: uppercase;
  font-weight: 700;
  font-size: clamp(0.75rem, 1vw + 0.1rem, 1.5rem);
}
.hero .container .content .cta:hover, .hero .container .content .cta:active, .hero .container .content .cta:focus {
  background-color: var(--clr-tertiary-a50);
  color: var(--clr-quinary);
  outline: none;
}

/*******************************************************************************
 OTHER SECTIONS
 ******************************************************************************/
section:not(.hero) {
  --clr-title: var(--clr-blackish);
  --clr-text: var(--clr-blackish);
  --clr-bg: var(--clr-whitish);
  padding-block: var(--space-xl);
  color: var(--clr-text);
  background-color: var(--clr-bg);
  font-size: clamp(1.25rem, 2vw + 0.1rem, 1.5rem);
  text-align: center;
}
@media (width >= 40em) {
  section:not(.hero) {
    text-align: left;
  }
  section:not(.hero).reverse > .two-columns {
    text-align: right;
  }
  section:not(.hero).reverse > .two-columns :first-child {
    order: 2;
  }
  section:not(.hero).reverse > .two-columns :first-child * {
    order: 0;
  }
  section:not(.hero).reverse > .two-columns :last-child {
    order: 1;
  }
}
section:not(.hero).primary {
  --clr-primary-a90: hsla(147, 100%, 14%, 0.9);
  --clr-title: var(--clr-quinary);
  --clr-text: var(--clr-whitish);
  --clr-bg: var(--clr-primary-a90);
}
section:not(.hero).secondary {
  --clr-title: var(--clr-whitish);
  --clr-text: var(--clr-quinary);
  --clr-bg: var(--clr-secondary);
}
section:not(.hero).tertiary {
  --clr-title: var(--clr-primary-a75);
  --clr-text: var(--clr-whitish);
  --clr-bg: var(--clr-tertiary-a75);
}
section:not(.hero).quaternary {
  --clr-title: var(--clr-blackish);
  --clr-text: var(--clr-primary-a75);
  --clr-bg: var(--clr-quaternary);
}
section:not(.hero).quinary {
  --clr-title: var(--clr-primary);
  --clr-text: var(--clr-blackish);
  --clr-bg: var(--clr-quinary);
}
section:not(.hero).image-bg {
  --clr-title: var(--clr-tertiary);
  --clr-text: var(--clr-quinary);
  --clr-bg: transparent;
  background: linear-gradient(0deg, var(--clr-blackish-a75), var(--clr-primary-a50)), url("../assets/photos/photo-flowers.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  text-shadow: 0 0 0.5em var(--clr-blackish-a75);
}
section:not(.hero) h1, section:not(.hero) h2, section:not(.hero) h3 {
  color: var(--clr-title);
  line-height: 1em;
}
section:not(.hero) .text {
  display: grid;
  place-content: center;
}
section:not(.hero) .text span {
  color: var(--clr-title);
  font-weight: 700;
}
section:not(.hero) .image {
  background-color: var(--clr-title);
  border: var(--border-height) solid var(--clr-title);
  border-radius: var(--border-radius);
  overflow: hidden;
}
section:not(.hero) .image img {
  height: 100%;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}
section:not(.hero) .image:hover:not(:active) {
  transform: scale(1.05);
  z-index: 3;
}
section:not(.hero).center {
  text-align: center;
}
section:not(.hero).center p {
  margin-inline: auto;
}

/*******************************************************************************
 RESET FORM
 ******************************************************************************/
button,
[type=button],
[type=reset],
[type=submit],
[type=file],
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
  cursor: pointer;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  box-sizing: border-box;
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/*******************************************************************************
 FORM
 ******************************************************************************/
form {
  margin-block: var(--space-l);
  padding: var(--space);
  background-color: var(--clr-tertiary-a75);
  border: var(--border-height) solid var(--clr-tertiary);
  border-radius: var(--border-radius);
  overflow: hidden;
  font-size: clamp(1.2rem, 2vw + 0.1rem, 1.5rem);
  font-weight: 700;
  text-shadow: 0 0 0.1em var(--clr-blackish-a25);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  gap: var(--space);
  align-items: center;
}
@media (width >= 40em) {
  form {
    padding: var(--space-l);
  }
}
@media (width >= 64em) {
  form {
    padding: var(--space-xl);
    grid-template-columns: auto 1fr;
    grid-template-rows: auto;
  }
}
form label {
  width: 100%;
  text-align: center;
}
@media (width >= 64em) {
  form label {
    text-align: right;
  }
}
form input, form select, form textarea {
  width: 100%;
  box-sizing: border-box;
  padding-inline: 1em;
  padding-block: 0.5em;
  color: var(--clr-quinary);
  border: var(--border-height) solid var(--clr-quinary);
  border-radius: var(--border-radius);
  background-color: var(--clr-quaternary-a50);
  font-size: inherit;
  font-weight: inherit;
  text-shadow: inherit;
}
form input:focus, form select:focus, form textarea:focus {
  color: var(--clr-quinary);
  border-color: var(--clr-quinary);
  background-color: var(--clr-tertiary);
  outline: 0;
}
form input::placeholder, form select::placeholder, form textarea::placeholder {
  opacity: 1;
}
form textarea {
  resize: vertical;
  font-family: inherit;
}
form input[type=submit] {
  width: fit-content;
  grid-column: 1/-1;
  justify-self: center;
  padding-inline: 2em;
  padding-block: 1em;
  color: var(--clr-quinary);
  border: var(--border-height) solid var(--clr-quinary);
  border-radius: var(--border-radius);
  background-color: var(--clr-quaternary-a50);
  text-transform: uppercase;
  font-weight: 700;
  font-size: clamp(1rem, 1.5vw + 0.1rem, 1.3rem);
}
form input[type=submit]:hover, form input[type=submit]:focus, form input[type=submit]:active {
  background-color: var(--clr-tertiary);
}

footer {
  padding-block: var(--space);
  color: var(--clr-whitish);
  background-color: var(--clr-blackish);
  font-size: clamp(0.8rem, 1vw + 0.1rem, 1rem);
}
footer svg {
  stroke: var(--clr-whitish);
  fill: var(--clr-whitish);
  vertical-align: bottom;
  height: 1em;
}
footer > .two-columns {
  align-items: center;
  text-align: center;
}
@media (width >= 40em) {
  footer > .two-columns :first-child {
    text-align: left;
  }
  footer > .two-columns :last-child {
    text-align: right;
  }
}
footer a.social-icon {
  margin-left: 1em;
  line-height: 2em;
}
footer a.social-icon svg {
  height: 2em;
}