@use '../utils' as *;

/*******************************************************************************
 OTHER SECTIONS
 ******************************************************************************/

section:not(.hero) {
    --clr-title: var(--clr-blackish);
    --clr-text: var(--clr-blackish);
    --clr-bg: var(--clr-whitish);
    padding-block: var(--space-xl);
    color: var(--clr-text);
    background-color: var(--clr-bg);
    font-size: clamp(1.25rem, 2vw + .1rem, 1.5rem);
    text-align: center;

    @include mq('md'){
        text-align: left;

        &.reverse > .two-columns {
            text-align: right;
    
            :first-child {
                order: 2;
    
                * {
                    order: 0;
                }
            }
    
            :last-child {
                order: 1;
            }
        }
    }

    &.primary{
        --clr-primary-a90: #{primary-color(.9)};
        --clr-title: var(--clr-quinary);
        --clr-text: var(--clr-whitish);
        --clr-bg: var(--clr-primary-a90);
    }

    &.secondary{
        --clr-title: var(--clr-whitish);
        --clr-text: var(--clr-quinary);
        --clr-bg: var(--clr-secondary);
    }

    &.tertiary{
        --clr-title: var(--clr-primary-a75);
        --clr-text: var(--clr-whitish);
        --clr-bg: var(--clr-tertiary-a75);
    }

    &.quaternary{
        --clr-title: var(--clr-blackish);
        --clr-text: var(--clr-primary-a75);
        --clr-bg: var(--clr-quaternary);
    }

    &.quinary{
        --clr-title: var(--clr-primary);
        --clr-text: var(--clr-blackish);
        --clr-bg: var(--clr-quinary);
    }

    &.image-bg{
        --clr-title: var(--clr-tertiary);
        --clr-text: var(--clr-quinary);
        --clr-bg: transparent;
        background: linear-gradient(0deg, var(--clr-blackish-a75), var(--clr-primary-a50)), url('../assets/photos/photo-flowers.jpg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        text-shadow: 0 0 .5em var(--clr-blackish-a75);
    }

    h1, h2, h3 {
        color: var(--clr-title);
        line-height: 1em;
    }

    .text {
        display: grid;
        place-content: center;

        span {
            color: var(--clr-title);
            font-weight: 700;
        }
    }

    .image {
        background-color: var(--clr-title);
        border: var(--border-height) solid var(--clr-title);
        border-radius: var(--border-radius);
        overflow: hidden;

        img {
            height: 100%;
            max-width: 100%;
            object-fit: cover;
            object-position: center;
        }

        &:hover:not(:active) {
            transform: scale(1.05);
            z-index: 3;
        }
    }

    &.center {
        text-align: center;

        p {
            margin-inline: auto;
        }  
    }
}