@use '../utils' as *;

footer {
    padding-block: var(--space);
    color: var(--clr-whitish);
    background-color: var(--clr-blackish);
    font-size: clamp(.8rem, 1vw + .1rem, 1rem);

    svg {
        stroke: var(--clr-whitish);
        fill: var(--clr-whitish);
        vertical-align: bottom;
        height: 1em;
    }

    & > .two-columns {
        align-items: center;
        text-align: center;

        @include mq('md') {
            :first-child {
                text-align: left;
            }
            :last-child {
                text-align: right;
            }
        }
    }

    a.social-icon{
        margin-left: 1em;
        line-height: 2em;

        svg {
            height: 2em;
        }
    }
}
